import MbtiCriteria from "../components/MbtiCriteria";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import VerticalCenter from "../components/VerticalCenter";
import CuteButton from "../components/CuteButton";
import CuteAlert from "../components/CuteAlert";
import styled, { ThemeProvider } from "styled-components";
import theme from "../components/styles/Theme.js";
import { useSetUser } from "../contexts/UserContext";
import LoadingScreen from "../components/LoadingScreen";

const Input = styled.input`
  font-size: 20px;
  width: 220px;
  margin-bottom: 30px;
  padding: 5px;
  border: solid 5px #9ab8fa;
  border-radius: 60px;
  background-color: white;
  outline: none;
  padding-left: 15px;
  line-height: 30px;
`;

const PinkText = styled.span`
  color: ${(props) => props.theme.hotpinkAccent};
  font-weight: bold;
`;

const DarkText = styled.p`
  color: ${(props) => props.theme.darkGrey};
  font-size: 23px;
  font-weight: 600;
`;

function ProfilePage() {
  const navigate = useNavigate();
  const setUser = useSetUser();

  const criteriaArray = [
    {
      criteria: "energy",
      criterialabel: " ",
      leftoption: "E",
      leftoptionlabel: "",
      rightoption: "I",
      rightoptionlabel: "",
    },
    {
      criteria: "info-processing",
      criterialabel: " ",
      leftoption: "S",
      leftoptionlabel: "",
      rightoption: "N",
      rightoptionlabel: "",
    },
    {
      criteria: "info-evaluation",
      criterialabel: " ",
      leftoption: "T",
      leftoptionlabel: "",
      rightoption: "F",
      rightoptionlabel: "",
    },
    {
      criteria: "lifestyle",
      criterialabel: " ",
      leftoption: "J",
      leftoptionlabel: "",
      rightoption: "P",
      rightoptionlabel: "",
    },
  ];

  const [myName, setMyName] = useState("");
  const [myMbti, setMyMbti] = useState(["", "", "", ""]);

  const [loading, setLoading] = useState(false);

  let changeMbti = (event, arrIndex) => {
    let newMbti = [...myMbti];
    newMbti[arrIndex] = event.target.defaultValue;
    setMyMbti(newMbti);
  };

  if (loading) {
    return <LoadingScreen />;
  }

  return (
    <ThemeProvider theme={theme}>
      <VerticalCenter>
        <DarkText>
          <PinkText></PinkText>  
        </DarkText>
        <Input
          type="text"
          onChange={(e) => {
            setMyName(e.target.value);
          }}
        />

        <div className="input-mbti">
          <DarkText>
            <PinkText>MBTI</PinkText>
            <span>  </span>
            {/* <div className="handclick-crop" /> */}
          </DarkText>

          <div>
            {criteriaArray.map((criteria, i) => (
              <MbtiCriteria
                key={`criteria${i}`}
                criteria={criteria.criteria}
                criterialabel={criteria.criterialabel}
                leftoption={criteria.leftoption}
                leftoptionlabel={criteria.leftoptionlabel}
                rightoption={criteria.rightoption}
                rightoptionlabel={criteria.rightoptionlabel}
                arrIndex={i}
                changeMbti={changeMbti}
              />
            ))}
          </div>
        </div>

        <div>
          <CuteButton
            to="/mybingo"
            onClick={async (e) => {
              e.preventDefault();

              if (myName === "") {
                CuteAlert(" !");
                return;
              }

              if (myMbti.includes("")) {
                CuteAlert("4    !");
                return;
              }

              setLoading(true);

              const response = await fetch("https://api.mbtibingo.com/users", {
                headers: {
                  Accept: "application/json",
                  "Content-Type": "application/json",
                },
                method: "POST",
                body: JSON.stringify({
                  uid: Date.now().toString(36) + Math.random().toString(36),
                  name: myName,
                  mbti: myMbti.join(""),
                }),
              });
              const body = await response.json();
              setUser(body);
              localStorage.setItem("uid", body["uid"]);
              setLoading(false);
              navigate("/mybingo");
            }}
          >
             
          </CuteButton>
        </div>
      </VerticalCenter>
    </ThemeProvider>
  );
}

export default ProfilePage;
